import {Component, Input, OnChanges, OnDestroy, SimpleChanges, TemplateRef, ViewChild} from '@angular/core';
import {xCalendar} from "../utils/xCalendar";
import {CalEvent} from "../utils/model/CalEvent";
import {CalAttachment} from "../utils/model/CalAttachment";
import {CalendarService} from "../calendar.service";
import {BsModalRef, BsModalService} from "ngx-bootstrap";
import {FileListComponent} from "../remote-module/filelist/list/file-list.component";

@Component({
    selector: 'calendar-attachment-list',
    templateUrl: './attachment-list.component.html',
    styleUrls: ['./attachment-list.component.css']
})
export class AttachmentListComponent implements OnChanges, OnDestroy {
    @Input() calEvent: CalEvent;
    @Input() fileEl: HTMLInputElement;
    @Input() removable: boolean;
    @ViewChild(FileListComponent) fileList: FileListComponent;
    files: any[] = [];
    modalRef: BsModalRef;
    confirmMessage: string;
    private _file;
    constructor(private calendarService: CalendarService,
                private modalService: BsModalService) {

    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.calEvent) {
            this._attachmentToFiles();
        }
    }

    ngOnDestroy(): void {
        if (this.modalRef) {
            this.modalRef.hide();
        }
    }

    viewAttachment(file, event: CalEvent) {
        window.open(xCalendar.getFileViewUrl(file.data, null, event));
    }

    removeAttachment(file, template: TemplateRef<any>) {
        if (file.id) {
            this._file = file;
            this.confirmMessage = 'Tp tin <b>"' + file.name + '"</b> s b xo?';
            this.modalRef = this.modalService.show(template);
        } else {
            this.fileList.removeFile(file);
        }
    }

    removeFile() {
        if (!this._file) {
            return;
        }
        this.modalRef.hide();
        this.calendarService.deleteAttachment({fileID: this._file.id, element: this.calEvent.uuid},() => {});
        this.fileList.removeFile(this._file);
        this._file = null;
    }

    private _attachmentToFiles() {
        if (this.calEvent && this.calEvent.attachments) {
            this.files = this.calEvent.attachments.map((item: CalAttachment) => {
                return {
                    name: item.file,
                    type: item.mimetype,
                    size: item.size,
                    id: item.gridfsUUID,
                    data: item
                };
            });
        } else {
            this.files = [];
        }
    }
}
