import {Component, OnDestroy, ViewChild, AfterViewInit, OnInit} from '@angular/core';
import {ActivatedRoute, NavigationEnd, Router} from '@angular/router';
import {CalendarService} from './calendar.service';
import {NotificationService, ResourceLoaderService} from 'inet-core';
import {xCalendar} from "./utils/xCalendar";
import {DateUtils} from "./utils/common/DateUtils";
import { Calendar } from '@fullcalendar/core';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import interactionPlugin from '@fullcalendar/interaction';
import {EventInput} from "@fullcalendar/core/structs/event";
import {OptionsInput, ViewOptionsInput} from "@fullcalendar/core/types/input-types";
import * as moment_ from "moment";
import {filter} from "rxjs/operators";
import {CalendarTodayComponent} from "./calendar-today/calendar-today.component";
import {CalendarUtils} from "./CalendarUtils";
const moment = moment_;

@Component({
    selector: 'inet-calendar',
    templateUrl: './calendar.component.html',
    styleUrls: ['./calendar.component.css'],
})
export class CalendarComponent implements AfterViewInit, OnDestroy {
    @ViewChild('calendarRef') calendarRef;
    @ViewChild(CalendarTodayComponent) calendarWidget: CalendarTodayComponent;
    viewTypes: CalendarViewMode[] = [
        new CalendarViewMode('Ngy', CalendarMode.timeGridDay, {
            columnHeaderText: (date: Date) => {
                let str = moment(date).format('dddd, D/M/Y');
                return str.slice(0, 1).toUpperCase() + str.slice(1);
            },
            titleFormat: () => CalendarUtils.displayDate(this.calendar.getDate())
        }),
        new CalendarViewMode('Tun', CalendarMode.timeGridWeek, {
            titleFormat: () => CalendarUtils.displayWeek(this.calendar.getDate())
        }),
        new CalendarViewMode('Thng', CalendarMode.dayGridMonth, {
            titleFormat: () => CalendarUtils.displayMonth(this.calendar.getDate())
        })
    ];
    defaultMode: CalendarMode = CalendarMode.dayGridMonth;
    viewTitle: string;
    calendarConfig: OptionsInput = {
        plugins: [dayGridPlugin, timeGridPlugin, interactionPlugin],
        themeSystem: 'bootstrap',
        firstDay: 1,
        locale: 'vi',
        allDaySlot: true,
        allDayText: 'C ngy',
        header: false,
        nowIndicator: true,
        navLinks: true, // can click day/week names to navigate views
        editable: true,
        droppable: true,
        eventLimit: true, // allow "more" link when too many events
        selectable: true,
        selectMirror: true,
        weekends: true,
        views: {},
        businessHours: {
            daysOfWeek: [ 1, 2, 3, 4, 5],  // Monday - Friday
            startTime: '00:01',
            endTime: '23:59'
        },
        eventDrop: (arg) => this._updateEventTime(arg),
        eventResize: (arg) => this._updateEventTime(arg),
        select: (arg) => this._selectRange(arg),
        eventClick: (arg) => this._viewEvent(arg),
        eventDragStart: () => this._hideViewDialog(),
        datesRender: () => this._calendarChange(),
        height: function () {
            return innerHeight - 59;
        }
    };
    calendar: Calendar;
    isToday: boolean;
    private _eventTimer: any;
    private _modeCache: string = '__calendar_mode__';
    private _routerObserver;
    private _events: any[] = [];
    private _currentRange: {start: Date, end: Date};
    constructor(
        private calendarService: CalendarService,
        private route: ActivatedRoute,
        private router: Router,
        private resourceLoader: ResourceLoaderService,
        private notify: NotificationService
    ) {
        // Apply cache config
        Object.assign(this.calendarConfig, this._getDataView());

        // Merge view config
        this.viewTypes.forEach((view) => {
            if (view.options) {
                this.calendarConfig.views[view.value] = view.options;
            }
        });

        // Use locale
        moment.locale(this.calendarConfig.locale.toString());
    }

    ngAfterViewInit() {
        this._init();
        this._hideViewDialog();
    }

    ngOnDestroy() {
        this.calendar.destroy();
        if (this._routerObserver) {
            this._routerObserver.unsubscribe();
        }
    }

    private _init() {
        this.calendar = new Calendar(this.calendarRef.nativeElement, this.calendarConfig);
        this.calendar.render();

        // Change date view
        this._routerObserver = this.router.events.pipe(filter(event => event instanceof NavigationEnd))
            .subscribe((event: NavigationEnd) => this._onNavigated(event));
    }

    private _updateEventTime(args) {
        let event;
        if (args.oldEvent) {
            event = this._events[Number(args.oldEvent.id)];
        } else {
            event = this._events[Number(args.event.id)];
        }
        if(event.mode !== 1){
            let toTime;
            if (args.event.allDay) {
                toTime = 23 * 60 + 59;
            } else {
                toTime = args.event.end.getHours() * 60 + args.event.end.getMinutes();
            }
            let params = {
                element: event.uuid,
                year: args.event.start.getFullYear(),
                day: DateUtils.dayOfYear(args.event.start),
                startTime: args.event.start.getHours() * 60 + args.event.start.getMinutes(),
                toTime: toTime,
                _allday: args.event.allDay
            };
            if (event.repeat) {
                Object.assign(params, event.repeat);
            }
            this.calendarService.updateEvent(params, (data, err) => {
                if (!data) {
                    this.notify.showMessage('Cp nht lch khng thnh cng: ' + err.status, 'error');
                    args.revert();
                    return;
                }
                this.reloadEvents();
            });
        } else {
            // Can't change event
            args.revert();
        }
    }

    createEvent() {
        let date = new Date();
        date.setHours(date.getHours() + 1, 0);
        this._createEvent(date, new Date(date.getTime() + 60 * 60 * 1000));
    }

    private _loadEvents(forceChange: boolean = false) {
        this._hideViewDialog();
        clearTimeout(this._eventTimer);
        this._eventTimer = setTimeout(() => {
            this._loadRangeEvents(forceChange);
        }, 500);
    }

    private _createEvent(start: Date, end: Date, allDay: boolean = false) {
        const dataBase64 = window.btoa(JSON.stringify({
            start: start.valueOf(),
            end: end.valueOf(),
            attribute: {
                allday: allDay + ''
            }
        }));
        this.router.navigate(['/calendar/create', dataBase64]);
        this._hideViewDialog();
    }

    private _selectRange(info) {
        this._createEvent(info.start, info.end, info.start.getTime() === (info.end.getTime() - 24 * 60 * 60 * 1000));
    }

    private _calendarChange() {
        if (this.calendar.view.type === CalendarMode.dayGridDay) {
            this.calendar.changeView(CalendarMode.timeGridDay);
            // Trigger change
            setTimeout(() => this._calendarChange());
            return;
        }
        setTimeout(() => {
            this._updateInfo();
            this._loadEvents();
        });
    }

    private _updateInfo() {
        this.viewTitle = this.calendar.view.title;
        this.viewTypes.forEach((view) => view.active = view.value === this.calendar.view.type);
        this._checkIsToday();
        this._updateViewParams();
    }

    private _checkIsToday() {
        const range = {
            start: this.calendar.view.activeStart,
            end: this.calendar.view.activeEnd,
        };
        this.isToday = this._dateInRange(new Date(), range);
    }

    private _loadRangeEvents(forceChange?: boolean){
        if (forceChange ||this._outOfRange()) {
            this._currentRange = this._getRange();
            this.calendarService.loadEvents(xCalendar.getRangeParams(this._currentRange.start, this._currentRange.end),
                (events) => this._renderEvents(events || []));
            this.calendar.removeAllEventSources();
        }
    }

    // Range view default is 6 week
    private _getRange() {
        let start = new Date(this.calendar.getDate());
        let end: Date;
        start.setDate(1);
        start.setHours(0, 0, 0, 0);
        while (start.getDay() !== this.calendarConfig.firstDay) {
            start.setDate(start.getDate() - 1);
        }
        end = new Date(start.getTime() + 6 * 7 * 24 * 60 * 60 * 1000);
        return {
            start: start,
            end: end
        };
    }

    private _outOfRange(): boolean {
        if (!this._currentRange) {
            return true;
        }
        return !(this._dateInRange(this.calendar.view.currentStart) && this._dateInRange(this.calendar.view.currentEnd));
    }

    private _dateInRange(date: Date, range?: {start: Date, end: Date}) {
        range = range || this._currentRange;
        return date >= range.start && date <= range.end;
    }

    private _renderEvents(events){
        this._events = events;
        let eventInputs = events.map((event, index) => {
            let eventInput: EventInput = {
                id: index,
                uuid: event.uuid,
                allDay: event.allDay,
                title: event.subject,
                start: event.start,
                end: event.end,
            };
            if (event.end < new Date()) {
                // Event on the past
                eventInput.textColor = '#999';
                eventInput.color = '#ddd';
            } else if (event.isCreator) {
                // Owner event
                eventInput.textColor = '#fff';
                eventInput.color = '#4da0d8';
            } else {
                // Other event
                eventInput.textColor = '#333';
                eventInput.color = '#99ed96';
            }
            return eventInput;
        });
        this._addEventSource(eventInputs);
    }

    private _viewEvent(args) {
        let event = this._events[Number(args.event.id)];
        this.calendarService.viewEvent(event, args.jsEvent, args.jsEvent.currentTarget, () => this.reloadEvents());
    }

    private _addEventSource(events) {
        this.calendar.addEventSource({
            events: events
        });
    }

    private _onNavigated(event: NavigationEnd) {
        const viewData = this._getDataView();
        this.calendar.changeView(viewData.defaultView, viewData.defaultDate);
    }

    changeView(type) {
        this.calendar.changeView(type.value);
    }

    onPrev() {
        this.calendar.prev();
    }

    onNext() {
        this.calendar.next();
    }

    onToday() {
        this.calendar.changeView(CalendarMode.timeGridDay, new Date());
    }

    private _getCalendarMode(mode: CalendarMode | string) {
        for (let i = 0; i < this.viewTypes.length; i++) {
            if (this.viewTypes[i].value === mode) {
                return this.viewTypes[i];
            }
        }
    }

    private _updateViewParams() {
        let params = [
            this.calendar.view.type,
            moment(this.calendar.getDate()).format('YYYY-M-D')
        ];
        this.router.navigate(['/calendar/v/' + params.join('/')]);
    }

    private _getDataView() {
        let segments = this.route.snapshot.url;
        let modeView = this._cacheModeView();
        let date: Date;
        if (segments.length > 1) {
            modeView = segments[0].path;
            date = new Date(segments[1].path);
        }
        if (!date || !date.getTime()) {
            date = new Date();
        }
        if (!this._getCalendarMode(modeView)) {
            modeView = this._getCalendarMode(this.defaultMode).value;
        }
        this._cacheModeView(modeView);
        return {
            defaultView: modeView,
            defaultDate: date,
        };
    }

    private _cacheModeView(modeView?: string) {
        if (modeView === undefined) {
            return localStorage.getItem(this._modeCache);
        } else {
            localStorage.setItem(this._modeCache, modeView);
        }
    }

    reloadEvents() {
        this._hideViewDialog();
        this._loadEvents(true);
        this.calendarWidget.loadTodayEvents();
    }

    private _hideViewDialog() {
        this.calendarService.hideViewModal();
    }
}

export class CalendarViewMode {
    name: string;
    value: CalendarMode;
    options: ViewOptionsInput;
    active: boolean = false;
    constructor(name: string, value: CalendarMode, options?: ViewOptionsInput) {
        this.name = name;
        this.value = value;
        this.options = options;
    }
}

export enum CalendarMode {
    dayGridDay = 'dayGridDay',
    timeGridDay = 'timeGridDay',
    timeGridWeek = 'timeGridWeek',
    dayGridMonth = 'dayGridMonth'
}