import {Injectable, ComponentFactoryResolver, Injector, ApplicationRef, ComponentRef} from '@angular/core';
import {SuggestionService} from 'inet-core';
import {xCalendar} from "./utils/xCalendar";
import {CalEvent} from "./utils/model/CalEvent";
import * as moment_ from "moment";
import { CalendarDialogViewComponent } from './dialog-view/dialog-view.component';
const moment = moment_;
declare let iNet: any;
moment.locale('vi');

@Injectable()
export class CalendarService {
    myOrg = {
        organId: iNet.organId,
        orgName: iNet.orgName,
        firmPrefix: iNet.firmPrefix
    };
    locationKeySuggestion = 'xcalendar_location';

    private _ready: boolean;
    private _fns: Function[] = [];
    private _viewModalRef: ComponentRef<CalendarDialogViewComponent>;

    constructor(
        private suggestService: SuggestionService,
        private componentFactoryResolver: ComponentFactoryResolver,
        private injector: Injector,
        private appRef: ApplicationRef,
    ) {
        xCalendar.roleUrl = 'unicorn/page/calendar/role';
        xCalendar.setOrgId(this.myOrg.organId);
        xCalendar.ready(() => {
            xCalendar.loadRole(this.myOrg.organId, (role) => {
                xCalendar.setOrgId(this.myOrg.organId);
                this._ready = true;
                this._triggerReady();
            });
        });
    }

    ready(callback: Function) {
        if (this._ready) {
            callback();
        } else {
            this._fns.push(callback);
        }
    }

    private _triggerReady() {
        this._fns.forEach(fn => fn());
        this._fns = [];
    }

    searchLocation(content: string, callback: Function) {
        if (!content) {
            return;
        }
        let params = {
            keyword: this.locationKeySuggestion,
            content: content
        };
        this.suggestService.loadSuggestion(params, callback);
    }

    saveLocation(content: string, callback: Function) {
        if (!content) {
            return;
        }
        let params = {
            keyword: this.locationKeySuggestion,
            content: content
        };
        this.suggestService.saveSuggestion(params, callback);
    }

    createEvent(params, callback) {
        this.ready(() => xCalendar.create(params, callback));
    }

    updateEvent(params, callback) {
        this.ready(() => xCalendar.update(params, callback));
    }

    removeEvent(params, callback) {
        this.ready(() => xCalendar.remove(params, callback));
    }

    attendeeSearch(params, callback) {
        this.ready(() => xCalendar.loadUserByKeyword(params, callback));
    }

    loadAttendees(callback) {
        this.ready(() => xCalendar.searchAttendee({}, callback));
    }

    loadEvents(params, callback) {
        this.ready(() => xCalendar.getListEvents(params, callback));
    }

    loadMonthEvents(date, callback) {
        this.ready(() => xCalendar.loadMonthEvents({}, date, callback));
    }

    loadEvent(params, callback) {
        this.ready(() => xCalendar.loadEvent(params, callback));
    }

    deleteAttachment(params, callback: Function) {
        this.ready(() => xCalendar.deleteAttachment(params,callback));
    }

    viewEvent(event: CalEvent, jsEvent: MouseEvent, target: HTMLElement, onChange: Function) {
        this._initViewModal();
        this._viewModalRef.instance.viewEvent(event, jsEvent, target, onChange);
    }

    hideViewModal() {
        if (this._viewModalRef) {
            this._viewModalRef.instance.hide();
        }
    }

    private _initViewModal() {
        if (this._viewModalRef) {
            return;
        }
        const componentFactory = this.componentFactoryResolver.resolveComponentFactory(CalendarDialogViewComponent);
        this._viewModalRef = componentFactory.create(this.injector);
        this.appRef.attachView(this._viewModalRef.hostView);
    }
}
