import {Component, Output, EventEmitter} from '@angular/core';
import {CalendarService} from "../calendar.service";
import {CalEvent} from "../utils/model/CalEvent";
import {DateUtils} from "../utils/common/DateUtils";

@Component({
    selector: 'calendar-widget-today',
    templateUrl: './calendar-today.component.html',
    styleUrls: ['./calendar-today.component.css']
})
export class CalendarTodayComponent {
    @Output() onChange = new EventEmitter<any>();
    events: CalEvent[] = [];
    colors = [
        // '#438EB9',
        '#007bff',
        '#3CA54A',
        '#FF9800',
        '#EF5B49',
        '#77901B',
        '#7D5392',
        '#808080'
    ];

    private _date: Date = new Date();

    constructor(private calendarService: CalendarService) {
        this.calendarService.ready(() => this.loadTodayEvents());
    }

    loadTodayEvents() {
        if (!this._date) {
            return;
        }
        this.calendarService.loadEvents({
            day: DateUtils.dayOfYear(this._date),
            year: this._date.getFullYear()
        }, (events) => {
            events.forEach((e) => this._initEventStatus(e));
            this.events = events.slice(0, 3); // Maximum 3 events
        });
    }

    onViewEvent(event: CalEvent, jsEvent: MouseEvent, targetEl: HTMLElement) {
        this.calendarService.viewEvent(event, jsEvent, targetEl, () => {
            this.loadTodayEvents();
            this.onChange.emit(true);
        });
    }

    private _initEventStatus(event) {
        let now = new Date();

        event.progressing = event.start < now && event.end > now;
        event.ended = event.end < now;
        event.pending = event.start > now;

        if (event.progressing) {
            event.statusText = 'ang din ra';
            event.statusColor = this.colors[0];
        } else if (event.pending) {
            event.statusText = 'Cha din ra';
            event.statusColor = this.colors[5];
        } else {
            event.statusText = 'Kt thc';
            event.statusColor = this.colors[6];
        }

        event.style = {
            'background-color': this.colors[event.mode]
        };
    }
}
