import {Component, DoCheck, EventEmitter, Input, IterableDiffers, Output} from '@angular/core';
import {CalAttendee} from "../utils/model/CalAttendee";
import {CalAttendeeState} from "../utils/model/CalAttendeeState";

@Component({
  selector: 'calendar-attendee-list',
  templateUrl: './attendee-list.component.html',
  styleUrls: ['./attendee-list.component.css']
})

export class CalendarAttendeeListComponent implements DoCheck {
  @Input() attendees: CalAttendee[] = [];
  @Input() editable: boolean = false;
  @Input() expandable: boolean = false;
  @Output() onDelete = new EventEmitter();

  differ: any;
  stateText: string;
  expandEnable: boolean = true;
  expanded: boolean = true;

  constructor(differs: IterableDiffers) {
    this.differ = differs.find([]).create(null);
  }

  ngDoCheck() {
    const change = this.differ.diff(this.attendees);

    // Detect push item on array
    if (change) {
      this.generateDetail();
    }
  }

  removeAttendee(attendee) {
    let index = this.attendees.indexOf(attendee);
    if (index > -1) {
      this.attendees.splice(index, 1);
      this.onDelete.emit();
    }
  }

  generateDetail() {
    let accept: number = 0,
        reject: number = 0,
        unknown: number = 0;
    let arr: string[] = [];

    this.attendees.forEach(function (item) {
        if (item.state === CalAttendeeState.ACCEPTED) {
            item._stateText = 'Tham d';
            accept++;
        } else if (item.state === CalAttendeeState.DECLINED) {
            item._stateText = 'Khng tham d';
            reject++;
        } else if (item.state === CalAttendeeState.NEEDS_ACTION) {
            item._stateText = 'Cha quyt nh';
            unknown++;
        }
    });

    if (accept) {
      arr.push(accept + ' c');
    }
    if (reject) {
      arr.push(reject + ' khng');
    }
    if (unknown) {
      arr.push(unknown + ' cha quyt nh');
    }
    this.stateText = arr.join(', ');
  }

  expandAttendees() {
    if (this.expandEnable) {
      this.expanded = !this.expanded;
    }
  }

  getStateIcon(attendee) {
    let cls;
    switch(attendee.state) {
      case 'ACCEPTED':
        cls = 'fa-check accept';
        break;
      case 'DECLINED':
        cls = 'fa-check accept';
        break;
      default:
        cls = 'fa-check unknown';
    }
    return 'attendee-info fa ' + cls;
  }
}
