import {Component, EventEmitter, Input, OnInit, Output, TemplateRef} from '@angular/core';

@Component({
    selector: 'file-list',
    templateUrl: './file-list.component.html',
    styleUrls: ['./file-list.component.css']
})
export class FileListComponent implements OnInit {
    @Input() fileEl: HTMLInputElement;
    @Input() files: FileListItem[] = [];
    @Input() removable: boolean = true;
    @Input() template: TemplateRef<any>;

    @Output() onClick = new EventEmitter<any>();
    @Output() onRemove = new EventEmitter<FileListItem>();

    ngOnInit(): void {
        this._initFileEl();
    }

    clickFile(file: FileListItem, event) {
        this.onClick.emit({file, event});
    }

    removeFile(file: FileListItem) {
        const index = this.files.indexOf(file);
        if (index > -1) {
            this.files.splice(index, 1);
            this.onRemove.emit(file);
        }
    }

    addFiles(files: FileList | File[]) {
        // Read as base64 if image
        for (let i  = 0; i < files.length; i++) {
            let file = files[i];
            if (this._fileIsImage(file)) {
                file['image'] = true;
                this._readImageBase64(file);
            }
            this.files.push(file);
        }
    }

    private _initFileEl() {
        if (this.fileEl) {
            this.fileEl.addEventListener('change', (e) => {
                const files = this.fileEl.files;
                if (files.length > 0) {
                    // Read as base64 if image
                    this.addFiles(files);

                    // Reset file
                    this.fileEl.value = '';
                }
            });
        }
    }

    private _readImageBase64(file) {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function() {
            file.url = reader.result;
        };
    }

    private _fileIsImage(file: FileListItem) {
        return file.type && file.type.indexOf('image') > -1;
    }
}

export interface FileListItem extends File {
    id?: string;
    url?: string;
    image?: boolean;
}
