import {
    AfterViewInit,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    OnDestroy,
    Output,
    ViewChild
} from '@angular/core';
import {CalendarService} from "../calendar.service";
import {CalEvent} from "../utils/model/CalEvent";
import {ModalDirective} from "ngx-bootstrap";
declare let $:any;

@Component({
    selector: 'calendar-dialog-delete',
    templateUrl: './dialog-delete.component.html',
    styleUrls: ['./dialog-delete.component.css']
})
export class CalendarDialogDelete implements AfterViewInit, OnDestroy{
    @Input() calEvent: CalEvent;
    @Output() onDelete = new EventEmitter();
    @ViewChild(ModalDirective) confirmDialog: ModalDirective;
    @ViewChild('dialogDelete') dialogDelete: ElementRef;

    dialogTitle: string = 'Lch cng tc';
    dialogContent: string = '';

    private _$dialogDelete;

    constructor(private calendarService: CalendarService) {}

    ngAfterViewInit(): void {
        this._initDialogDelete();
    }

    ngOnDestroy(): void {
        this._destroyDialogDelete();
    }

    hide() {
        this.confirmDialog.hide();
    }

    delete() {
        this.calendarService.removeEvent({
            element: this.calEvent.uuid,
            orgid: this.calEvent.firmUUID
        }, () => {
            this.hide();
            this.onDelete.emit();
        });
    }

    show() {
        this.dialogContent = 'Bn c ng  xa s kin <b>"' + this.calEvent.subject + '"</b> khng?';
        this.confirmDialog.show();
    }

    private _initDialogDelete() {
        this._$dialogDelete = $(this.dialogDelete.nativeElement).hide();

        // Click outside to close
        this.hide = this.hide.bind(this);
        document.body.addEventListener('click', this.hide);

        // Prevent close
        this._$dialogDelete.on('click', (e) => e.stopPropagation());

        // Append modal to body
        $(document.body).append(this._$dialogDelete);
    }

    private _destroyDialogDelete() {
        document.body.removeEventListener('click', this.hide);
        this._$dialogDelete.remove();
    }
}
