import {Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild} from '@angular/core';
import {TimepickerComponent} from "ngx-bootstrap";
declare let $:any;

@Component({
    selector: 'time-picker-modal',
    templateUrl: './time-picker-modal.component.html',
    styleUrls: ['./time-picker-modal.component.css']
})
export class TimePickerModalComponent implements OnInit, OnDestroy {
    @Input() arrowkeys: boolean = true;
    @Input() hourStep: number = 1;
    @Input() max: Date;
    @Input() min: Date;
    @Input() minuteStep: number = 15;
    @Input() mousewheel: boolean = true;
    @Input() readonlyInput: boolean = false;
    @Input() secondsStep: number = 10;
    @Input() showMeridian: boolean;
    @Input() showMinutes: boolean = true;
    @Input() showSeconds: boolean;
    @Input() showSpinners: boolean = true;

    @Input() set time(date: Date | null) {
        this._timeDate = date;
        this.timeStr = TimePickerModalComponent._formatDate(date);
        this.onChange.emit(this.timeStr);
    }

    get time(): Date {
        return this._timeDate;
    }

    @Output() onChange = new EventEmitter();
    @ViewChild('timePicker') timePicker: TimepickerComponent;
    timeStr: string;
    $modal: any;
    $modalContent: any;
    private _timeDate: Date;

    constructor(
        public el: ElementRef
    ) {
    }

    ngOnInit() {
        this.$modal = $(this.el.nativeElement).detach().addClass('modal');
        this.$modalContent = this.$modal.find('.time-picker-modal');

        // Touched close
        this.$modal.on('mousedown click', (e) => {
            if (e.target.isSameNode(this.$modal[0])) {
                this.close();
            }
        })
    }

    ngOnDestroy() {
        this.$modal.remove();
    }

    close() {
        this.$modal.detach();
    }

    open(css: any) {
        this.$modalContent.css(css);
        document.body.appendChild(this.$modal[0]);
    }

    private static _formatDate(date: Date) {
        if (!date || !date.getTime()) {
            return '';
        } else {
            return ('0' + date.getHours()).slice(-2) + ':' + ('0' + date.getMinutes()).slice(-2);
        }
    }
}
