import {Directive, ElementRef, EventEmitter, forwardRef, HostListener, Input, OnInit, Output} from '@angular/core';
import {TimePickerModalComponent} from "./time-picker-modal/time-picker-modal.component";
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from "@angular/forms";

export const DEFAULT_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => TimePickerModalDirective),
  multi: true
};

@Directive({
  selector: '[timePicker]',
  providers: [DEFAULT_VALUE_ACCESSOR]
})
export class TimePickerModalDirective implements OnInit, ControlValueAccessor {
  @Output() valueChange = new EventEmitter<string>();
  @Input() timePickerModal: TimePickerModalComponent;
  @HostListener('focus') onFocus() {
    this.open();
    this.onChange();
  }
  @HostListener('change') onChange() {
    this.timePickerModal.time = this.getDateInput();
  }
  private propagateChange;
  constructor(private el: ElementRef) { }

  ngOnInit() {
    if (!this.timePickerModal) {
      console.error('[TimePickerModalDirective] Missing timePickerModal');
    }
    this.timePickerModal.onChange.subscribe((timeStr) => {
      this.writeValue(timeStr);
      if (this.propagateChange) {
        this.propagateChange(timeStr);
      }
    });
  }

  writeValue(value: string) {
    this.el.nativeElement.value = value;
    this.valueChange.emit(value);
  }

  registerOnChange(fn: any) {
    this.propagateChange = fn;
  }

  registerOnTouched(fn: any) {
    
  }

  open() {
    let bounding = this.el.nativeElement.getBoundingClientRect();
    let css = {
      left: bounding.left
    };
    if (bounding.top + 150 < innerHeight) {
      // Show bottom
      css['top'] = bounding.top + bounding.height + 2;
    } else {
      // Show top
      css['bottom'] = bounding.top - 2;
    }
    this.timePickerModal.open(css);
  }

  close() {
    this.timePickerModal.close();
  }

  private getDateInput(): Date | null {
    let value = this.getValue();
    if (!value) {
      return null;
    }
    let date = new Date();
    date.setMinutes(0);
    // HH:mm:ss format
    let times: any[];
    if (value.indexOf(':') > -1) {
      times = value.split(':');
    } else {
      times = [];
      for (let i = 0; i < value.length + 1; i += 2) {
        times.push(value.substr(i, 2));
      }
    }
    times[0] = Number(times[0]);
    times[1] = Number(times[1]) || 0;
    // Invalid time
    if (!times[0] && times[0] !== 0) {
      return null;
    }
    date.setHours(times[0]);
    date.setMinutes(times[1]);
    return date;
  }

  private getValue() {
    return this.el.nativeElement.value.trim();
  }
}
